import { createWebHistory, createRouter } from "vue-router";
import Product from './components/appProduct'
import Service from './components/appService'
import Company from './components/appCompany'
const routes = [
    {
        path: '/', 
        component: Product
    },
    {
        path: '/service', 
        component: Service
    },
    {
        path: '/company', 
        component: Company
    },
]

const router = createRouter({
    history: createWebHistory(), routes,
   });
   export default router; 