<template lang="">
  <div id="container">
    <h2>coffee list</h2>
    <div class="in_wrap" style="width: 40%; margin: auto;">
      <div class="content" v-for="(c, index) in coffees" :key="index">
        <div>
          <h3>{{c.title}}</h3>
          <img :src="c.image" @click="detile(c)">
        </div>
      </div>
    </div>
    <button v-if="more" @click="addlist" class="more">더보기</button>
    <modal v-if="modal" :propsdata="detileData" @closeModal="closeModal"/>
  </div>
</template>
<script>
import coffee from '/src/assets/coffee.js'
import modal from './Modal/appModal'
import axios from 'axios'

export default {
  data() {
    return {
      coffees:coffee,
      modal : false,
      detileData : null,
      more : true,
      cont2 : 2
    }
  },
  methods: {
    detile(c){
      this.modal = true;
      this.detileData = c
    },
    closeModal(){
      this.modal = false
    },
    addlist(){
        axios.get(`https://joonsol.github.io/data/more${this.cont2}.json`).then( result => {
          result.data.forEach(param => {
            this.coffees.push(param)
            
          });
          //this.cont2 = this.cont2 == 0? 1 : 0
          //console.log(result)
        }).catch( ()=>{  this.more = false  })
    }
  },
  components:{
    modal
  },
}
</script>
